/* ==========================================================================
   WooCommerce Message Responsive styles - begin
   ========================================================================== */

@media only screen and (max-width: $ipad-landscape) {

    .qodef-woocommerce-page {

        .woocommerce-message,
        .woocommerce-info,
        .woocommerce-error {
            line-height: 46px;
        }
    }
}

@media only screen and (max-width: $phone-landscape) {

    .qodef-woocommerce-page {

        .woocommerce-message,
        .woocommerce-info,
        .woocommerce-error {
            padding: 20px 20px 10px;
            line-height: 2em;
            text-align: left;

            a.button {
            	float: none;
                width: 100% !important;
                text-align: center;
                margin: 10px 0;
            }
        }
    }
    
    .woocommerce-page .qodef-content {
        .woocommerce-message,
        .woocommerce-info,
        .woocommerce-error {
	        a.button {
		        margin-bottom: 5px !important;
	        }
        }
    }
}
/* ==========================================================================
   WooCommerce Message Responsive styles - end
   ========================================================================== */