/* ==========================================================================
   WooCommerce Cart page responsive styles - begin
   ========================================================================== */

@include ipad-landscape {

	.qodef-woocommerce-page {

		table.cart {

			td.actions {

				.button {
					margin: 0px 0 0 22px;
					padding: 10px 45px;
					font-size: 14px;
				}

			}
		}
	}
}

@include ipad-portrait {
	
	.qodef-woocommerce-page {
		
		table.cart {
			
			td.actions {
				
				.coupon {
					
					input[type="text"] {
						width: 180px;
						margin: 0 17px 0 0;
					}
				}

				.button {
					margin: 1px 0 0 22px;
				}
			}
		}
		
		.cart-collaterals table th {
			padding-right: 20px;
		}
	}
}

@include phone-landscape {
	
	.qodef-woocommerce-page {
		
		table.cart {

			thead tr th {
				padding: 0 8px 0 0;
				box-sizing: border-box;
				font-size: 14px;
				
				&.product-thumbnail {
					display: none;
				}
			}
			
			tr {
				
				th, td {
					font-size: 12px;
					line-height: 1.5em;
					
					&.product-quantity {
						padding-left: 10px;
						padding-right: 10px;
					}
				}
				
				&.cart_item {

					td {
						
						&.product-thumbnail {
							display: none;
						}
					}
				}
			}
			
			td.actions {
				
				.coupon {
					margin: 0 0 20px;
					
					input[type="text"] {
						width: 100%;
						padding: 9px 15px;
						margin: 0 10px 20px 0;
					}
				}

				.button {
					margin: 0;
					width: 100%;
				}
				
				.coupon,
				> input {
					float: none;
					display: block;
				}
			}
		}
	}
}
/* ==========================================================================
   WooCommerce Cart page responsive styles - end
   ========================================================================== */