/* ==========================================================================
   WooCommerce Single Product Page Responsive styles - begin
   ========================================================================== */

@media only screen and (min-width: $ipad-landscape-plus-pixel) and (max-width: $laptop-landscape) {
    
    .qodef-woo-single-page {
        
        .woocommerce-tabs {
            
            ul.tabs {
                
                > li {
                    
                    a {
                        padding: 12px 7px;
                    }
                }
            }
        }
    }
}

@media only screen and (min-width: $ipad-landscape-plus-pixel) and (max-width: $laptop-landscape-mac) {

    .qodef-woo-single-page {

        .qodef-single-product-content {
	
	        .images {
		        width:65%
	        }
	        
            .qodef-single-product-summary {
	            
	            width: 35%;

                .woocommerce-product-details__short-description {

                    p {
                        margin: 0 14% 0 0;
                    }
                }
            }
        }
    }
}

@media only screen and (min-width: $ipad-landscape-plus-pixel) and (max-width: $laptop-landscape-medium) {
	
	.qodef-woo-single-page .qodef-single-product-content {
		
		.images {
			width:65%
		}
		
		.qodef-single-product-summary {
			width: 35%;
		}
	}
}

@include ipad-landscape {
    
    .qodef-woo-single-page {
        
        .qodef-single-product-content {
            
            .images,
            .qodef-single-product-summary {
                float: none;
                width: 100%;
                padding: 0;
            }
	        
	        .images {
		
		        .woocommerce-product-gallery__trigger {
			        right: 10px;
		        }
	        }
            
            .qodef-single-product-summary {
                margin: 30px 0 0;
            }

            button[type=submit]:not(.qodef-search-submit) {
                padding: 12px 43px;
            }
        }
        
        .woocommerce-tabs {
            
            ul.tabs {
                
                > li {
                    
                    a {
                        padding: 13px 10px;
                    }
                }
            }
        }
    }
}

@media only screen and (min-width: 1280px) and (max-width: 1366px) {

    .qodef-woo-single-page {

        &.qodef-woo-single-thumb-on-left-side {

            .qodef-single-product-content {

                .images {

                    .woocommerce-product-gallery__image {

                        &:first-child {
                            width: calc(100% - 90px);
                        }
                    }
                }
            }
        }
    
        &.qodef-woo-pl-info-below-image {
        
            .related.products,
            .upsells.products {
            
                .product {
                    margin: 0 0 40px;
                }
            }
        }
    }
}

@include ipad-landscape {
	.qodef-woo-single-page.qodef-woo-pl-info-below-image {
		
		.related.products,
		.upsells.products {
			
			.product {
				margin: 0 0 40px;
			}
		}
	}
}

@include phone-landscape {
    
    .qodef-woo-single-page {
	
	    &.qodef-woo-single-thumb-on-left-side {
		
		    .qodef-single-product-content {
			
			    .images {
				
				    &.woocommerce-product-gallery--columns-4 {
					
					    .woocommerce-product-gallery__image {
						
						    &:not(:first-child) {
							    width: 50%;
						    }
					    }
				    }
			    }
		    }
	    }
        
        .qodef-single-product-content {
            
            .images {
    
                &.woocommerce-product-gallery--columns-4 {
        
                    .woocommerce-product-gallery__image {
            
                        &:not(:first-child) {
	                        width: 50%;
                        }
                    }
                }
            }
        }

        .woocommerce-tabs {

            ul.tabs {

                > li {
                    padding-left: 0;

                    a {
                        padding-left: 0;
                    }
                }
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Single Product Page Responsive styles - end
   ========================================================================== */